/* add css module styles here (optional) */
body {
  background-color: #252d4a;
  /* background-color: #ffffff; */
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Lato", sans-serif;
}



.recorder_library_box .recorder_box_inner {
  min-height: 300px;

  background: #212121;
  border-radius: 0 0 3px 3px;
  display: flex;
  flex-direction: column;
}

.recorder_library_box .mic_icon {
  width: 60px;
  display: flex;
  height: 60px;
  position: fixed;
  justify-content: center;
  align-items: center;
  transition: 0.5s ease all;
  background: rgb(245, 0, 87);
  border-radius: 50%;
  bottom: 65px;
  right: 20%;
  color: #fff;
  font-size: 25px;
}

/* .recorder_library_box .microphone_icon_sec {
  width: 25px;
  height: 25px;
  display: flex;
} */

.recorder_library_box .mic_icon:hover {
  background-color: #fff;
}

.recorder_library_box .mic_icon_svg {
  fill: #fff;
  width: 100%;
  height: 100%;
}

.recorder_library_box .mic_icon:hover .mic_icon_svg {
  fill: rgb(245, 0, 87);
}

.recorder_library_box .reco_header {
  display: flex;
  justify-content: space-between;
  background: #bd9f61;
  align-items: center;
  padding: 20px 20px;
  color: #fff;
  border-radius: 3px 3px 0 0;
}

.recorder_library_box .reco_header .h2 {
  font-weight: 400;
}

.recorder_library_box .reco_header .close_icons {
  font-size: 20px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  cursor: pointer;
  transition: 0.5s ease all;
}

/* .recorder_library_box .reco_header .close_icons:hover {
  background: rgba(123, 118, 106, 0.21);
} */

.recorder_library_box .record_section {
  position: relative;
  flex: 1;
}

.recorder_library_box .record_section .mic_icon {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 20px;
}

