.recorder_library_box .btn_wrapper .clear_btn {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  top: 10em;
  background-color: #bd9f61;
}

.recorder_library_box .btn_wrapper .clear_btn[disabled],
.recorder_library_box .btn_wrapper .upload_btn[disabled] {
  cursor: not-allowed;
  color: #d9d9d9;
  background: #f5f5f5;
  border-color: #d9d9d9;
  text-shadow: none;
  box-shadow: none;
}

.recorder_library_box .btn_wrapper .clear_btn:hover {
  background: #185fec;
  color: #fff;
}

.recorder_library_box .duration {
  text-align: center;
}

.recorder_library_box .recorder_page_box {
  min-height: calc(100vh - 128px);
  background: #fff;
}

.recorder_library_box .duration * {
  color: #fff;
  font-size: 60px;
}

.recorder_library_box .duration_section .help {
  color: #fff;
}

.recorder_library_box .record_controller {
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  bottom: 0px;
  padding: 20px 0;
  display: flex;
}

.recorder_library_box .record_controller .icons {
  width: 50px;
  display: flex;
  height: 50px;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  color: #fff;
  margin-right: 15px;
  font-size: 20px;
}

.recorder_library_box .record_controller .stop {
  background: #940505;
  position: relative;
}

.recorder_library_box .record_controller .pause {
  background: #9c6702;
  position: relative;
}

.recorder_library_box .play_icons {
  width: 20px;
  height: 20px;
  border-style: solid;
  border-width: 10px 0px 10px 17px;
  border-color: transparent transparent transparent #fff;
  box-sizing: border-box;
  position: absolute;
  left: 56%;
  transform: translate(-50%, -50%);
  top: 50%;
}

.recorder_library_box .pause_icons {
  width: 17px;
  height: 17px;
  border-style: double;
  border-width: 0px 0px 0px 15px;
  border-color: #fff;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.recorder_library_box .stop_icon {
  width: 17px;
  height: 17px;
  border: 1.5px solid #fff;
  border-radius: 2px;
}

.recorder_library_box .microphone_icon_sec {
  width: 30px;
  height: 30px;
  display: flex;
}
